<template>
    <yd-layout>
        <div class="detail">
            <div class="detailContent">
                <div class="meetingTitle"><span class="leftBorder"></span>{{detail.title}}</div>
                <!-- <yd-flexbox>
                    <div class="work" v-show="itemType!=undefined&&itemType!=null&&itemType!=''">{{itemType}}</div>
                    <div class="work" v-show="detail.type==undefined">会议</div>
                </yd-flexbox> -->
                <yd-flexbox>
                    <div>开始时间：</div>
                    <yd-flexbox-item v-text="startTime"></yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox>
                    <div>结束时间：</div>
                    <yd-flexbox-item v-text="endTime"></yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox v-show="address!=''&&address!=undefined">
                    <div>日程地点：</div>
                    <yd-flexbox-item v-text="address"></yd-flexbox-item>
                </yd-flexbox>
                <!-- <yd-flexbox v-show="neirong!=''">
                    <div>日程内容：</div>
                    <div class="textCot" v-html="neirong"></div>
                </yd-flexbox> -->
                <div class="leftPosition" v-show="neirong!=''&&neirong!=undefined">
                    <div class="meetMaterial">日程内容：</div>
                    <div class="materialName">
                        <div class="theMaterial" v-html="neirong">
                        </div>
                    </div>
                </div>
                <yd-flexbox>
                    <div>紧急程度：</div>
                    <yd-flexbox-item v-text="emergency"></yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox>
                    <div>提醒方式：</div>
                    <yd-flexbox-item v-text="remindWay"></yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox>
                    <div>公开范围：</div>
                    <yd-flexbox-item v-text="openRange"></yd-flexbox-item>
                </yd-flexbox>
                <div class="edit" @click="toUpdata($route.params.id)">编辑日程</div>
            </div>
            <div class="return" @click="$router.go(-1)">
                <div><img src="../../../static/images/close@2x.png"></div>
            </div>
        </div>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    name: 'detailsSchedule',
    data() {
        return {
            detail: {},
            startTime: '',
            endTime: '',
            remindWay: '',
            emergency: '', // 紧急程度
            openRange: '', // 公开范围
            neirong: '', // 日程内容
            itemType: '', // 事项类型
            itemList: '', // 事项类型集合
            address: ''   // 地点
        }
    },
    mounted() {
        this.getSchedules();
        document.setTitle('日程详情');
    },
    computed: {
        ...mapGetters(['getUserInfo'])
    },
    methods: {
        getSchedules() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/Calendar/mobileInterface/calendarDetail', {
                'ssotoken': vm.getUserInfo.empcode,
                'id': vm.$route.params.id
            }, function (res) {
                console.log(res.jsonData);
                vm.detail = res.jsonData;
                vm.startTime = vm.format(vm.detail.begintime);
                vm.endTime = vm.format(vm.detail.endtime);
                vm.neirong = vm.detail.remark;
                vm.address = vm.detail.address;
                console.log(res.jsonData.address);
                if (vm.detail.urgentflg == 1) {
                    vm.emergency = '紧急';
                } else if (vm.detail.urgentflg == 0) {
                    vm.emergency = '一般';
                }
                if (vm.detail.remindtype == 0) {
                    vm.remindWay = '及时';
                } else if (vm.detail.remindtype == 15) {
                    vm.remindWay = '提前15分钟';
                } else if (vm.detail.remindtype == 30) {
                    vm.remindWay = '提前30分钟';
                } else if (vm.detail.remindtype == -1) {
                    vm.remindWay = '不提醒';
                }
                if (vm.detail.scope == '169') {
                    vm.openRange = '私密';
                } else if (vm.detail.scope == '170') {
                    vm.openRange = '全公司';
                } else if (vm.detail.scope == '171') {
                    vm.openRange = '全部门';
                } else if (vm.detail.scope == '172') {
                    vm.openRange = '可查看我日程的人（见门户）';
                }
                // for (let i = 0; i < vm.itemList.length; i++) {
                //     if (vm.detail.type == vm.itemList[i].id) {
                //         vm.itemType = vm.itemList[i].name;
                //     }
                // }
                console.log(vm.detail.type)
            });
        },
        add0(m) { return m < 10 ? '0' + m : m },
        format(shijianchuo) {
            // shijianchuo是整数，否则要parseInt转换
            var time = new Date(shijianchuo);
            var y = time.getFullYear();
            var m = time.getMonth() + 1;
            var d = time.getDate();
            var h = time.getHours();
            var mm = time.getMinutes();
            return y + '-' + this.add0(m) + '-' + this.add0(d) + ' ' + this.add0(h) + ':' + this.add0(mm);
        },
        toUpdata(id) {
            var vm = this;
            vm.$router.push(`/addeditSchedule/${id}/edit`)
        },
        // 获取事项类型 紧急程度 提醒方式 公开范围
        getBase() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/Calendar/mobileInterface/getBase', {
                'ssotoken': vm.getUserInfo.empcode
            }, function (res) {
                // 事项类型
                // vm.itemList = res.jsonData.itemList;
                // 公开范围
                // vm.dataArr5 = res.jsonData.scopeList;
                vm.getSchedules();
            });
        }
    }
}
</script>
<style lang="less" scoped>
.detailContent {
    min-height: 100%;
    position: relative;
    .edit {
        position: absolute;
        bottom: 0.4rem;
        left: 0;
        color: #59a5fd;
        font-size: .3rem;
        text-align: center;
        width: 100%;
    }
}

.textCot {
    color: #666;
    line-height: .5rem;
}

.detail {
    height: 91%;
    // position: relative;
}

body {
    background: #f2f2f2;
}

.meetingTitle {
    margin-bottom: .1rem;
}

.return {
    height: .75rem;
    width: 100%;
    margin-top: .24rem;
    div {
        width: .75rem;
        border-radius: 50%;
        background: #c8c8c8;
        height: .75rem;
        line-height: .76rem;
        display: inline-block;
    }
    img {
        width: .3rem;
        padding-bottom: .04rem;
    }
    text-align: center;
    color: #fff;
    font-size: .5rem;
}

.work {
    // width: 1.5rem;
    height: .44rem;
    background: #59a5fd;
    color: #fff;
    line-height: .44rem;
    text-align: center;
    font-size: .28rem;
    border-radius: .06rem;
    margin: .15rem 0;
    padding: 0 .1rem;
}

.openBtn {
    color: #59a5fd;
    margin-left: .38rem;
    display: inline-block;
}

.yd-btn {
    width: 25%;
    height: .85rem;
    line-height: .85rem;
    margin-right: .1rem;
    margin-bottom: .35rem;
    color: #fff;
    font-size: .3rem;
}

.yd-scrollview {
    background-color: #f8f8f8;
}

.trsTextarea {
    min-height: 2.4rem;
    background-color: #f2f2f2;
    border-radius: 5px;
    color: #999;
    font-size: .3rem;
    padding: .4rem;
}

.finsh {
    color: #59a5fd;
    text-align: center;
    font-size: .3rem;
    margin-top: .4rem;
}

.remove {
    height: .75rem;
    width: .75rem;
    background-color: #ccc;
    margin: 0 auto;
    border-radius: .75rem;
    margin-top: .24rem;
    padding-top: .21rem;
}

.close {
    width: .32rem;
    height: .32rem;
    margin: 0 auto;
    display: block;
}

.openPart {
    min-height: 0.8rem;
    line-height: .8rem;
    overflow: hidden;
    font-size: .3rem;
    color: #333;
    .attendLeader {
        width: 1.5rem;
        float: left;
    }

    .leaderName {
        float: left; // height: 0.8rem;
        // overflow: hidden;
        .textPart {
            display: inline;
            width: 3.25rem;
        }
    }
}

.theMaterial {
    color: #333!important;
}
</style>


